"use client";
import React from 'react';
import { Users, BookOpen, ShieldCheck, Zap, Globe, Lock } from 'lucide-react';
import { motion } from 'framer-motion';
import styles from './FeaturesSection.module.css';

const FeatureCard = ({ icon: Icon, title, description, variant = '' }) => {
    const cardVariants = {
        hidden: { opacity: 0, y: 20 },
        visible: {
            opacity: 1,
            y: 0,
            transition: {
                duration: 0.6,
                ease: [0.21, 0.47, 0.32, 0.98]
            }
        }
    };

    return (
        <motion.div
            variants={cardVariants}
            className={`${styles.card} ${styles[variant] || ''}`}
        >
            <div className={styles.cardIcon}>
                <Icon size={24} />
            </div>
            <h3 className={styles.cardTitle}>{title}</h3>
            <p className={styles.cardDesc}>{description}</p>
        </motion.div>
    );
};

const FeaturesSection = () => {
    const containerVariants = {
        hidden: { opacity: 0 },
        visible: {
            opacity: 1,
            transition: {
                staggerChildren: 0.1,
            }
        }
    };

    return (
        <section className={styles.section}>
            <div className={styles.container}>
                <motion.div
                    className={styles.heading}
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    transition={{ duration: 0.6 }}
                >
                    <h2 className={styles.title}>Engineered for Excellence</h2>
                    <p className={styles.subtitle}>
                        Everything you need to manage a modern university at scale.
                    </p>
                </motion.div>

                <motion.div
                    className={styles.bentoGrid}
                    variants={containerVariants}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, margin: "-100px" }}
                >
                    <FeatureCard
                        icon={Users}
                        title="Student Management"
                        description="Comprehensive student lifecycle tracking from admission to graduation."
                        variant="cardLarge"
                    />
                    <FeatureCard
                        icon={BookOpen}
                        title="Academic Engine"
                        description="Powerful curriculum builder and automated scheduling system."
                    />
                    <FeatureCard
                        icon={ShieldCheck}
                        title="Enterprise Security"
                        description="Role-based access control and encrypted data storage."
                    />
                    <FeatureCard
                        icon={Zap}
                        title="Real-time Analytics"
                        description="Instant insights into academic performance and financial health."
                        variant="cardLarge"
                    />
                </motion.div>
            </div>
        </section>
    );
};

export default FeaturesSection;
