"use client";
import React from 'react';
import Link from 'next/link';
import { GraduationCap } from 'lucide-react';
import { motion } from 'framer-motion';
import styles from './Navbar.module.css';

const Navbar = () => {
    return (
        <motion.header
            className={styles.navbarWrapper}
            initial={{ y: -20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.6, ease: [0.21, 0.47, 0.32, 0.98] }}
        >
            <div className={styles.container}>
                <Link href="/" className={styles.logo}>
                    <GraduationCap size={28} color="var(--primary-green)" />
                    <span style={{ fontWeight: 800, letterSpacing: '-0.03em' }}>Yenepoya <span style={{ color: 'var(--primary-green)' }}>ERP</span></span>
                </Link>

                <nav className={styles.navLinks}>
                    <Link href="/#features" className={styles.link}>Features</Link>
                    <Link href="/#solutions" className={styles.link}>Solutions</Link>
                    <Link href="/#pricing" className={styles.link}>Pricing</Link>
                    <Link href="/#about" className={styles.link}>About</Link>
                </nav>

                <div className={styles.ctaGroup}>
                    {typeof window !== 'undefined' && localStorage.getItem('token') ? (
                        <>
                            <Link
                                href={`/${localStorage.getItem('role')}/dashboard`}
                                className={styles.signInBtn}
                            >
                                Dashboard
                            </Link>
                            <button
                                onClick={() => {
                                    localStorage.removeItem('token');
                                    localStorage.removeItem('role');
                                    window.location.href = '/login';
                                }}
                                className={styles.getStartedBtn}
                            >
                                Logout
                            </button>
                        </>
                    ) : (
                        <>
                            <Link href="/login" className={styles.signInBtn}>
                                Sign in
                            </Link>
                            <Link href="/login" className={styles.getStartedBtn}>
                                Get Started
                            </Link>
                        </>
                    )}
                </div>
            </div>
        </motion.header>
    );
};

export default Navbar;
